.app{
  background: #27A2CF;
background: linear-gradient(to bottom right, #27A2CF 9%, #3ACF44 64%);
  box-shadow: -22px 18px 5px 0px rgba(0,0,0,0.75);
-webkit-box-shadow: -22px 18px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: -22px 18px 5px 0px rgba(0,0,0,0.75);
  display: block;
  align-items: center;
  justify-content: space-evenly;
  width: 50%;
  
  padding:20px;
  margin-top: 50px;
  

}
.Question-section{
 font-weight: bolder;
 font-size: large;
}
.questionCount{
font-size: 30px;

}
.question-text{
padding-top: 10px;
}
.answer-section{
  display: flex;
  flex-direction: column;
  
  
}
.button{
 background-color: transparent;
  margin-top: 20px;
  padding: 20px;
  font-size: 30px;
  background: #27A2CF;
  background: linear-gradient(to bottom right, #27A2CF 9%, #3ACF44 64%);
  border: 5px green solid;
  border-radius: 30px;
  cursor: pointer;
}
.button :hover{
  border: 5px rebeccapurple solid;
  opacity: 0.3;
}
.score{
  text-align: center;
  padding-top: 100px;
  padding-bottom: 100px;
}